@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #e1dcc5;
    height: 100%;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-size: 13pt;
}

.box {
    width: 400px;
    padding: 40px;
    position: absolute;
    top: 50%;
    left: 25vw;
    background: #343a40;
    text-align: center;
    transition: 0.25s;
    margin-top: 100px;
    border-radius: 15px;
}

#basic-nav-dropdown {
    color: #fff;
}

.box input[type="text"],
.box input[type="password"] {
    border: 0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 1px solid #17a2b8;
    padding: 10px 10px;
    outline: none;
    color: #17a2b8;
    border-radius: 24px;
    transition: 0.25s
}

.login {
    width: 250px;
}

.box h1 {
    color: white;
    text-transform: uppercase;
    font-weight: 500
}

.box input[type="text"]:focus,
.box input[type="password"]:focus {
    width: 300px;
    box-shadow: 0 0 5px #17a2b8;
}

.box input[type="text"]:hover,
.box input[type="password"]:hover {
    box-shadow: 0 0 5px #17a2b8;
}

.submit {
    border: 0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 1px solid #17a2b8;
    padding: 14px 40px;
    outline: none;
    color: #17a2b8;
    border-radius: 15px;
    transition: 0.25s;
    cursor: pointer
}

.submit:hover {
    background: #17a2b8;
    color: #fff;
}

.forgot {
    text-decoration: underline;
}

.text {
    color: #fff;
}

@media (max-width: 400px) {
    .box {
        left: -1% !important;
        width: 350px;
    }

    .nav-item {
        margin: 0 !important;
    }
}

@media (max-width: 800px) {
    .box {
        left: -2%;
    }

    .nav-item {
        margin-right: 25px;
    }

    .button {
        margin: 0 5px;
    }
}

@media (max-height: 700px) {
    .box {
        margin-top: 50px;
    }
}
.home {
    position: relative;
    height: 55vh;
}

/* Search Bar */
#search {
    width: 95%;
    display: flex;
    margin-top: 25px;
    margin-left: 2.5vw;
    justify-content: center;
}

.search-before {
    position: absolute;
    top: 50%;
}

.input:focus,
.input:hover,
.fields:focus,
.fields:hover {
    outline: none;
    box-shadow: 0 0 5px #000;
}

.input {
    width: 35vw;
    height: 45px;
    border-right: none;
    padding-left: 15px;
    border-radius: 5px 0 0 5px;
}

.input:focus {
    width: 36.5vw;
    border-color: rgba(0, 0, 0, 0.6) !important;
}

.fields {
    width: 80px;
    height: 45px;
    border-left: none;
    text-align: center;
    background-color: #e2dcc552;
    border-radius: 0 5px 5px 0;
}

.search-button {
    height: 44px;
    background-color: #343a40;
    width: 100px;
}

.search-button:hover {
    background-color: #17a2b8;
    cursor: inherit !important;
}

.search-button:focus {
    outline: none;
}

@media (max-width: 400px) {
    .search-before {
        margin-left: 7px;
    }

    .input {
        width: 50vw;
    }

    .search-button {
        width: 70px;
        margin-right: 0;
    }

    .fields {
        width: 55px;
    }
}

/* Search Results */
.search-results {
    width: 90%;
    margin: 15px auto;
}

.book-list {
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
    justify-content: center;
}

@media (max-width: 350px) {
    .search-results {
        width: 100%;
    }

    .book-list {
        padding: 15px;
    }
}

/* Book Result div */
.book {
    width: 250px;
    height: 475px;
    margin: 15px 20px;
    padding-top: 15px;
    border-radius: 5px;
    background-color: #f9f9f9;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.book-cover {
    margin: 10px auto;
    text-align: center;
    overflow: hidden;
    width: 200px;
    height: 289px;
}

.book-title, .book-author {
    margin: 0;
    overflow: hidden;
}

.book-title {
    height: 60px;
    font-weight: 700;
    font-size: 1.1rem;
}

.book-author {
    height: 30px;
}

.published-year {
    margin: 0 auto 5px auto;
}

.add-button {
    margin: 0;
    width: 100%;
    height: 35px;
    border: none;
    padding: 0;
    color: #fff;
    border-radius: 0 0 5px 5px;
    background-color: rgb(3, 118, 184);
}

.published-year {
    font-size: 0.8em;
}

/* Error Paragraph */

#error-title {
    margin-top: 25vh;
}
@media (max-width: 350px) {
    .navbar {
        width: 100%;
        padding: 15px 5px;
    }
}

.site-title {
    font-family: 'Fredericka the Great';
    font-size: 35pt;
    color: #e1dcc5 !important;
}

.site-title:focus,
.site-title:hover,
.nav-link:focus,
.nav-link:hover {
    outline: none !important;
    text-decoration: underline;
}


